<template>
  <header class="landing">
    <nav>
      <span class="logo">icom</span>
      <ul>
        <li><router-link to="/">Home</router-link></li>
        <li><router-link to="/">About us</router-link></li>
        <li><router-link to="/">Services</router-link></li>
        <li><router-link to="/">Portfolio</router-link></li>
        <li><a href="#contact" @click="$router.push('/')">Contact us</a></li>
      </ul>
      <button @click="toggleMenu">
        <i class="fas fa-bars fa-2x"></i>
        <i class="fas fa-times fa-2x"></i>
      </button>
    </nav>
    <section>
      <img v-if="section.image" :src="section.image" :alt="section.name" />
      <h2 v-if="section.name">{{ section.name }}</h2>
      <slot></slot>
    </section>
  </header>
</template>

<script>
export default {
  name: "landing",
  props: {
    section: {
      type: Object,
    },
  },
  methods: {
    toggleMenu() {
      document.querySelector("header nav").classList.toggle("menu-shown");
    },
  },
};
</script>

<style lang="scss">
header {
  height: 100vh;
  width: 100%;
  color: white;
  background: linear-gradient(
    220.1deg,
    rgba(32, 23, 72, 1) 0%,
    rgba(75, 43, 132, 1) 100%
  );
  display: flex;
  flex-flow: column nowrap;
  section {
    display: flex;
    flex-flow: column wrap;
    height: 400px;
    width: 100%;
    align-items: center;
    justify-content: space-around;
    margin: auto;
    h2 {
      font-size: max(4vw, 2em);
      max-width: max(65%, 320px);
    }
    img {
      height: 100%;
      max-height: 40vw;
    }
  }
  nav {
    z-index: 4;
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    padding: 20px 60px;
    justify-content: space-between;
    button {
      display: none;
      z-index: 5;
      background: transparent;
      border: none;
      outline: none;
      color: white;
    }
    span.logo {
      font-size: 1.6em;
      font-weight: bold;
    }
    ul {
      list-style: none;
      padding: 0;
      li {
        margin-left: 4em;
        display: inline-block;
        a {
          padding: 6px 10px;
          transition: background-color 0.2s ease;
          border-radius: 40px;
          &:hover {
            background-color: rgba(255, 255, 255, 0.1);
          }
        }
        &:last-of-type {
          margin-left: 6em;
          a {
            border: 2px solid var(--accent);
            &:hover {
              background-color: var(--accent);
            }
          }
        }
      }
    }
  }
}
@media (max-width: 950px) {
  header {
    nav {
      padding: 20px 30px;
      .fa-bars {
        display: inline;
      }
      .fa-times {
        display: none;
      }
      &.menu-shown {
        ul {
          transform: translateY(0);
        }
        .fa-bars {
          display: none;
        }
        .fa-times {
          display: inline;
        }
      }
      button {
        display: block;
      }
      ul {
        // display: none;
        margin: 0;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        background-color: var(--accent);
        transform: translateY(-100%);
        transition: transform 0.4s ease-out;
        li {
          font-size: 1.4em;
          display: block;
          margin: 2em !important;
        }
      }
    }
  }
}
</style>
